/**
 *  用户认证接口参数
 */
auth.prototype.LoginParams = function(){
    const ob = {};
    if (process.env.NODE_ENV === "development" || process.env.NODE_ENV === "pre") {
        // 测试环境针对不是河南的机顶盒获取不到userID的问题兼容
        if (yh.userId === "" || yh.userId === null){
            ob.userId = 'dxrmcs';
        }else{
            ob.userId = yh.userId;
        }
    }else{
        ob.userId = yh.userId;
    }
    ob.action = "1";
    ob.deviceMac = yh.mac;
    ob.ipAddress = yh.ip;
    const obString = JSON.stringify(ob);
    console.log("auth login" + obString);
    return obString;
}

/**
 *  用户鉴权接口参数
 *  auth:Jiang jie
 *  time： 2018-06-01 11:20
 * @param {*} contentBelong 
 * @param {*} spCode 
 * @param {*} serviceCodes 
 * @param {*} contentCode 
 */
auth.prototype.AuthParams = function (productCode, spCode, contentCode) {
    console.log("接收产品包ID:" + productCode + "^^^^^^^siteId:" + yh.bizDomain);
    let siteId = yh.bizDomain;
    const ob = {};
    if (siteId === null || siteId === "") {
        console.log("站点ID异常，获取盒端CDN类型判断");
        if(yh.cdn==2){
            siteId = "1001";
        }else{
            siteId = "1000";
        }
    }
    ob.contentBelong = siteId || '';
    ob.userId = yh.userId;
    ob.userToken = yh.userToken || '';
    ob.serviceCodes = spCode;
    ob.productCode = productCode;
    ob.contentCode = contentCode;
    const obString = JSON.stringify(ob);
    console.log("auth params" + obString);
    return obString;
}

/**
 *  用户订购接口参数
 *  auth:Jiang jie
 *  time： 2018-06-01 11:20
 * @param {*} contentBelong  所属站点
 * @param {*} returnUrl 返回地址
 * @param {*} spCode 服务商编码
 * @param {*} productCode 产品编码
 * @param {*} contentCode 内容编码（按次订购时必填）
 * @param {*} contentName 内容名称（按次订购时必填）
 */
auth.prototype.OrderParams = function (productCode, contentCode, contentName) {
    const ob = {};
    ob.contentBelong = yh.bizDomain;
    ob.userId = yh.userId;
    ob.userToken = yh.userToken;
    ob.productCode = productCode;
    ob.contentCode = contentCode;
    ob.contentName = contentName;
    const obString = JSON.stringify(ob);
    // console.log("Order params" + obString);
    return obString;
}

/**
 *  订购查询接口    
 * @param {*} contentBelong 
 * @param {*} orderType 
 * @param {*} includeExpired 
 * @param {*} page 
 * @param {*} size 
 */
auth.prototype.OrderListParams = function (orderType, includeExpired, page, size) {
    const ob = {};
    ob.contentBelong = yh.bizDomain;
    ob.userId = yh.userId;
    ob.userToken = yh.userToken;
    ob.orderType = orderType;
    ob.includeExpired = includeExpired;
    ob.page = page;
    ob.size = size;
    const obString = JSON.stringify(ob);
    console.log("Order params" + obString);
    return obString;
}

let Authentication = new auth();

export { Authentication}


