import Vue from 'vue'
import Router from 'vue-router'

const Category = () => import(/* webpackChunkName: "main" */'../page/navigationBars/category.vue')

const Detail = () => import(/* webpackChunkName: "detail" */'../page/detail/Category_Movie.vue')

const NotFound = () => import(/* webpackChunkName: "other" */'../page/notFound.vue')

const actor = () => import(/* webpackChunkName: "hotTopics" */'../page/detail/actor.vue')

const Search = () => import(/* webpackChunkName: "menu" */'../page/search/Search.vue')

const HistoryHome = () => import(/* webpackChunkName: "menu" */'../page/historyHome/historyhomes.vue')

const firm = () => import(/* webpackChunkName: "other" */'../page/firm.vue')

const Filter = () => import(/* webpackChunkName: "menu" */'../page/filter/FilterResult.vue')

const Special = () => import(/* webpackChunkName: "hotTopics" */'../page/Special/Special.vue')

const AllSpecial = () => import(/* webpackChunkName: "hotTopics" */'../page/Special/AllSpecial.vue');

const mainPage = () => import(/* webpackChunkName: "main" */'../page/MainTemplate.vue')

const VIP = () => import(/* webpackChunkName: "system" */'../page/heNan/VipOrder.vue')

const User = () => import(/* webpackChunkName: "system" */'../page/heNan/UserCenter.vue')

const CooperativeBootom = () => import(/* webpackChunkName: "other" */'../page/cooperative/Cooperative_Nav_Wrap.vue') 

const CoopMain = () => import(/* webpackChunkName: "other" */'../page/cooperative/Cooperative_Main.vue') 

const UserAndDeviceInfo = () => import(/* webpackChunkName: "system" */'../page/heNan/UserInfoAndSystemInfo.vue') 

const AppManage = () => import(/* webpackChunkName: "system" */'../page/heNan/AppManage.vue') 

const SearchBox = () => import(/* webpackChunkName: "other" */'../page/heNan/Category_SearchBox.vue') 

const Dailog = () => import(/* webpackChunkName: "other" */'../dailog/dailog.vue') 

const PayMent = () => import ( /* webpackChunkName: "other" */'../tool/pay/payment.vue')

const OrderBg = () => import ( /* webpackChunkName: "other" */'../page/heNan/Vip_introduce.vue')


Vue.use(Router)

export default new Router({
  base:'/henan/',
  mode:'history',
  routes: [
    { path: '/', redirect: '/index'},
    {
      path: '/', name: 'category', component: Category, props: true, meta: { keepAlive: true },
      children:[
        { path: 'index', name: 'index', component: mainPage, props: true, meta: { keepAlive: true }},
        { path: 'south', name: 'south', component: mainPage, props: true, meta: { keepAlive: true }}
      ]
    },
    { path: '/Special', name: 'Special', component: Special, props: true, meta: { keepAlive: false }},
    { path: '/AllSpecial', name: 'AllSpecial', component: AllSpecial, props: true, meta: { keepAlive: false }},
    { path: '/detail', name: 'detail', component: Detail, props: true, meta:{keepAlive: false }},
    { path: '/actor', name: 'actor', component: actor, props: true, meta: { keepAlive: false }},
    { path: '/search', name: 'search', component: Search, props: true, meta: { keepAlive: true }},
    { path: '/history', name: 'history', component: HistoryHome, props: true, meta: { keepAlive: true },},
    { path: '/firm', name: 'firm', component: firm, props: true, meta: { keepAlive: false }},
    { path: '/filter', name: 'filter', component: Filter, props: true, meta: { keepAlive: true } },
    { path: '/vip', name: 'vip', component: OrderBg, props: true, meta: { keepAlive: false } },
    { path: '/user', name: 'user', component: User, props: true, meta: { keepAlive: true } },
    { path: '/userAndDeviceInfo', name: 'userAndDeviceInfo', component: UserAndDeviceInfo, props: true, meta: { keepAlive: true } },
    { path: '/appManage', name: 'appManage', component: AppManage, props: true, meta: { keepAlive: false } },
    {
      path: '/hz', name: 'hz', component: CooperativeBootom, props: true, meta: { keepAlive: true }, 
      children: [
        { path: '/zq', name: 'hzindex', component: CoopMain, props: true, meta: {keepAlive: true} }
      ]},
    { path: '/Test', name: 'test', component: SearchBox, props: true, meta: { keepAlive: false } },
    { path: '/dailog', name: 'dailog', component: Dailog, props: true, meta: { keepAlive: false } },
    { path: '/payment', name: 'payment', component: PayMent, props: true, meta: { keepAlive: false } },
    { path: '*', name: 'all', component: NotFound, props: true,meta:{keepAlive: true }},
  ]
})



