//  JavaScript常用方法
import {client as yh} from '../js/client'
import {xha} from '../js/xha'
export default {
  clearLocal(localName) { // 清除本地缓存
      localStorage.removeItem(localName);
  },
  setLocal(localName, localStr){ //  设置本地缓存
      localStorage.setItem(localName, localStr);
  },
  getLocal(localName){ // 获取本地缓存
    var localName = localStorage.getItem(localName);
    if(this.isAllNull(localName)) {
      return "";
    } else {
      return localName;
    }
  },
  isAllNull(str) { // 验证参数合法性
    if (null == str || "null" == str || "" === str || "undefined" == str || undefined == str){
      return true;
    }
    return false;
  },strAllNull(str) {// 是否是字符串
    if(this.isAllNull(str)) {
      return "";
    }
    return str;
  },
  routerSkip(url,Type,bp,route){ // 公共路由跳转
    // 路由跳转
     console.log("路由:url:" + url + "===========路由Type:" + Type);
    // 1：详情页 2：演员页 3：角色 4：专题   5:短视频  6：标签  7：分类 8:历史收藏 9：搜索 10：筛选
     switch (Type) {
        case "1": // 详情页
          route.push({name:"detail",params:{jsonUrl:url,bp:bp}});
          break;
        case "2":// 演员列表页
          route.push({ name: "actor", params: { jsonUrl: url,bp:bp}});
         break;
       case "3":
          console.log("暂无此页面");
         break;
       case "4":
         route.push({ name: "Special", params: { jsonUrl: url, bp: bp } });
         break;
       case "5":
         route.push({ name: "AllSpecial", params: { jsonUrl: url, bp: bp } });
         break;
       case "6":
         route.push({ name: "Special", params: { jsonUrl: url, bp: bp } });
         break;
       case "7":
         route.push({ name: "Special", params: { jsonUrl: url, bp: bp } });
         break;
       case "8": // 第三方外链或app
         if (url != "" && url != undefined) { 
           if (url.indexOf('apk://') >= 0) { // apk打开下载
             try {
               const apkInfo = url.split('$');
               let jsonOb = {};
               jsonOb.packageName = apkInfo[1];
               jsonOb.downloadUrl = apkInfo[2];
               jsonOb.apkMd5 = apkInfo[3].toLowerCase();
               jsonOb.appName = apkInfo[4];
               console.log("APK下载地址:" + JSON.stringify(jsonOb));
               let EnterType = 2;
               try {
                let jsonArray = yh.getAppList("getAppList");
                let appList= JSON.parse(jsonArray);
                 for (let index = 0; index < appList.length; index++) {
                   const element = appList[index];
                   if (element.appName == jsonOb.appName){
                     console.log(jsonOb.appName+"已下载")
                     EnterType = 3;
                   }
                }
               } catch (error) {
                 console.log("过滤已安装列表失败"+error);
               }
               if (EnterType == 3){
                 xha.app(jsonOb.appName, "1", EnterType);
               }
               yh.downLoadApk(JSON.stringify(jsonOb));
             } catch (error) {
               console.log("请求参数有误~");
             }
           } else if (url.indexOf('gatewayStandIndex_henan_forward.jsp') >= 0) { // 聚精彩专区
                yh.showPage(url+this.getBsetfunned());
           }else{ // 跳转第三方外链或专区
              const isSouth = localStorage.getItem("isSouth");
              if (isSouth!=undefined||isSouth!=null) {
                if(url.indexOf('http')==0){
                  yh.showPage(this.getOtherInfo(url)); // 第三方地址打开
                }else{ // 百事通专区打开
                  yh.BSTPage(this.getOtherInfo(url));// 携带用户id及用户通token
                }
              }else{
                  yh.showPage(this.getOtherInfo(url)); // 第三方地址打开
              }
           }
         }
         break;
       case "9": // 搜索
         route.push({ name: "vip", params: { jsonUrl: url, bp:bp} });
         break;
       case "10":// 筛选
         route.push({ name: "filter", params: { jsonUrl: url, bp: bp } });
         break;
       case "11":// 个人中心
         route.push({ name: "user", params: { jsonUrl: url, bp: bp } });
         break;
       case "12":// 专区
         route.push({ name: "hzindex", params: { jsonUrl: url, bp: bp } });
         break;
       case "13":// 设备信息
         route.push({ name: "userAndDeviceInfo", params: {type: bp } });
         break;
        case "14": // 应用管理
         route.push({ name: "appManage", params: {type: bp } });
         break;
        case "15": // 点播记录
         route.push({ name: "history", params: { jsonUrl: url, bp: bp } });
         break;
        case "16": // 点播记录
        route.push({ name: "search", params: { jsonUrl: url, bp: bp ,type:'index'} });
         break;
    }

  },formatDate(ms){ // 时间格式化
      let oDate = new Date(ms),
      oYear = oDate.getFullYear(),
      oMonth = oDate.getMonth() + 1,
      oDay = oDate.getDate(),
      oHour = oDate.getHours(),
      oMin = oDate.getMinutes(),
      oSen = oDate.getSeconds(),
      oTime = oYear  + getzf(oMonth) + getzf(oDay)  + getzf(oHour)  + getzf(oMin)  + getzf(oSen);
     return oTime;
  },getSouthHistoryData(elementName,assetId,assetImg,jsonUrl,assetType){
    // 获取豫南观看历史记录数据
      let newHistoryArr = window.localStorage.getItem('historyArr')
      let historyArr = null;
      if( newHistoryArr == null){
        historyArr = []
      }else{
        historyArr = JSON.parse(newHistoryArr);
        if(historyArr.length>50){
          historyArr = []
        }
        for (let index = 0; index < historyArr.length; index++) {
          if( historyArr[index].assetId == assetId){
            historyArr.splice(index,1)
          }
        }
      }
        let objJ = {}
        objJ.recordTime = new Date()-0;
        objJ.elementName = elementName;
        objJ.assetName = elementName;
        objJ.assetId = assetId;
        objJ.assetImg = assetImg;
        objJ.jsonUrl = jsonUrl;
        objJ.assetType = assetType;
        historyArr.push(objJ)
        let historyArrS = JSON.stringify(historyArr)
        window.localStorage.setItem('historyArr',historyArrS) 
  },setPosition(arg1,arg2){ // 保存点播推荐位置
    localStorage.setItem("position", arg1 + "_" + arg2);
  },getPosition(name){
    const pos = localStorage.getItem("position");
    let sp = pos.split('_');
    if (name === "posId"){
      return sp[0];
    } else if (name === "columnId"){
      return sp[1];
    }
  },removePosition(){
    localStorage.setItem("position","");
  },
  setTrackPath(pathId,pathType){ // 保存点播路径
    let ppvPath =  sessionStorage.getItem('ppvPath');
    let ppvPathType = sessionStorage.getItem('ppvPathType');
    let group="";
    let groupType = "";
    if(this.isAllNull(ppvPath)){
       group = pathId;
    }else{
       group = ppvPath+"|"+pathId;
    }
    if (this.isAllNull(ppvPathType)) {
      groupType = pathType;
    } else {
      groupType = ppvPathType + "|" + pathType;
    }
    sessionStorage.setItem('ppvPath', group);
    sessionStorage.setItem('ppvPathType', groupType);
  },getTrackPath(){ // 获取当前点播路径id
    let ppvPath = sessionStorage.getItem('ppvPath');
    return ppvPath;
  },getTrackPathType() { // 获取当前点播路径
    let PathType = sessionStorage.getItem('ppvPathType');
    return PathType;
  },clearTrackPath(){
    sessionStorage.setItem('ppvPath', '');
    sessionStorage.setItem('ppvPathType', '');
  },getParentPageType(){ // 获取父级页面类型
    let PathType = sessionStorage.getItem('ppvPathType');
    if (this.isAllNull(PathType)){
       return "";
    }else{
      let Path = PathType.split('|');
      return Path[Path.length-2]
    }
  }, getParentPageId() { // 获取父级页面ID
    let PathType = sessionStorage.getItem('ppvPath');
    if (this.isAllNull(PathType)) {
      return "";
    } else {
      // return PathType.split('|').slice(-2,1).join('');
      try {
        let Path = PathType.split('|');
        return Path[Path.length-2]
      }catch (error){
        console.log(error)
      }
    }
  },BPTypefilters(catCode){ // 统计分类code
    switch (catCode) {
      case 'recommend':
          return "01"
        break;
      case 'Live':
        return "02"
        break;
      case 'YPYS': // VIP
        return "03"
        break;
      case 'Demand':
        return "04"
        break;
      case 'series':
        return "06"
        break;
      case 'movie':
        return "05"
        break;
      case 'Education':
        return "07"
        break;
      case 'Entertainment':
        return "08"
        break;
      case 'Myself':
        return "09"
        break;
    }
  },getBsetfunned(){// 聚精彩专区;
    let TokenExpiretime = this.formatDate(new Date().getTime() + 24 * 60 * 60 * 1000);
    let stringBuffer = "<userId>" + yh.userId + "</userId><userToken>" + yh.userToken + "</userToken><TokenExpiretime>" + TokenExpiretime + "</TokenExpiretime><GroupId></GroupId><userIP></userIP><areaCode>0" + yh.areaCode + "</areaCode><TradeId></TradeId><key></key><stbId></stbId><VAStoEPG></VAStoEPG><back_epg_url>" + location.href + "</back_epg_url>";
    return stringBuffer;
  },getOtherInfo(url){ // 第三方外链携带用户信息
    let operators = "zx";
    if(yh.cdn==='1'){ // 豫北中兴
      operators = 'zx';
    }else if(yh.cdn=='2'){ // 豫南华为
      operators = 'hw';
    }
    let  value = ''
    if(url.indexOf('171.14.99.98') === -1){
      value = "UserId=" + yh.userId + "&UserToken=" + yh.userToken + "&PlatForm=" + operators;
    }else{
      value = "epg_info=<account>" + yh.userId + "</account>";
    }
    if(url.indexOf('?')>=0){
      url = url +"&"+value;
    }else{
      url = url + "?" + value;
    }
    return url;
  },removeCollect(assetId){ // 删除豫北收藏记录
      const historyList = localStorage.getItem('PersonMovies');
      if (!this.isAllNull(historyList)){
          const temh = JSON.parse(historyList);
          for (let index = 0; index < temh.length; index++) {
            const element = temh[index];
            if (element.assetId === assetId) {
               console.log("删除收藏资产ID：" + assetId);
               temh.splice(index,1);
               localStorage.setItem('PersonMovies', JSON.stringify(temh));
               break;
            }
          }
    }
  }, removeHistoryS(assetId) { // 豫南删除历史
    const historyList = localStorage.getItem('historyArr');
    if (!this.isAllNull(historyList)) {
      const temh = JSON.parse(historyList);
      for (let index = 0; index < temh.length; index++) {
        const element = temh[index];
        if (element.assetId === assetId) {
          console.log("删除豫南历史记录ID：" + assetId);
          temh.splice(index, 1);
          localStorage.setItem('historyArr', JSON.stringify(temh));
          break;
        }
      }
    }
  },getQueryString(url, name) { // 提取字符串中的参数
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
    var r = url.substr(1).match(reg);
    if (r != null) {
      return unescape(r[2]);
    }
    return null;
  }, logCsSplice(EnterType, ColumnID, ColumnName, ProductID, SPID, CatCode,AssetID,FilmName,Episodes,AllEpisodes,FilmType) { // BS透传CS参数
      let logs = {};
      logs.EnterType = EnterType;
      logs.ColumnID = ColumnID;
      logs.ColumnName = ColumnName;
      logs.ProductID = ProductID;
      logs.SPID = SPID;
      logs.CatCode = CatCode;
      logs.AssetID = AssetID;
      logs.FilmName = FilmName;
      logs.VodEpisode = Episodes;
      logs.VodEpisodes = AllEpisodes;
      logs.FilmType = FilmType;
      logs.StartTime = "0";
      logs.VodTime = "0";
      const logStr =  JSON.stringify(logs);
      console.log('B透传C客户端log参数===================' + logStr);
      yh.logCsParams(logStr);
  }, ynPlayLog(ColumnID, ColumnName, AssertID, FilmName, Episodes, VodEpisodes, EnterType, FilmType, State, ProductID, SPID){
     const logs = {};
    logs.ColumnID = ColumnID;
    logs.ColumnName = ColumnName;
    logs.AssetID = AssertID;
    logs.FilmName = FilmName;
    logs.Episodes = VodEpisodes
    logs.VodEpisode = Episodes
    logs.VodEpisodes = VodEpisodes;
    logs.EnterType = EnterType;
    logs.FilmType = FilmType;
    logs.StartTime= '0';
    logs.State = State;
    logs.ProductID = ProductID;
    logs.SPID = SPID;
    logs.Reservel1 = "";
    logs.Reservel2 = '';
    console.log("豫南华为播放器透传参数:"+JSON.stringify(logs));
    return JSON.stringify(logs);
  }

}
function getzf(num) {
  if (parseInt(num) < 10) {
    num = '0' + num;
  }
  return num;
}

window.downLoadApk = function(jsonStr){
  console.log('apk下载回调');
  console.log(jsonStr);
  console.log(JSON.parse(jsonStr));
  
}
/**
 *  对Date的扩展，将 Date 转化为指定格式的String
 * 月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
 * 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
 * 例子：
 * (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
 * (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
 */
// eslint-disable-next-line no-extend-native
Date.prototype.format = function (fmt) {
  // author: meizz
  'use strict'
  var o = {
    'M+': this.getMonth() + 1, // 月份
    'd+': this.getDate(), // 日
    'h+': this.getHours(), // 小时
    'm+': this.getMinutes(), // 分
    's+': this.getSeconds(), // 秒
    'q+': Math.floor((this.getMonth() + 3) / 3), // 季度
    S: this.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (this.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return fmt
}



