import * as types from '../types'

/**
 * App通用配置
 */


const state = {
  rootPath: '',
  catList: JSON.parse(localStorage.getItem('HenanRoot')) || {},
  category: JSON.parse(localStorage.getItem('Henan_Category')) || [],
  behindParams: '&returnType=jsonp',
  isHTTPS: localStorage.getItem('ISHTTPS') == 'true' || false,
  httpRequestTime: 0,
  httpResponseTime: 0,
  navpos: 0,
  auth: false,
  authStatus: false,
  loadStatus: false,
  isSouth: false,
  episode: 0,
  areaCode: 0,
  liveStatus: false,
  noScroll: false,
  indexKey: false,
  appsNum: 0,
  downloadAppName:''
}
const getters = {
  rootPath: state => state.rootPath,
  catList:state => state.catList,
  category:state => state.category,
  behindParams:state=> state.behindParams,
  isHTTPS:state=> state.isHTTPS,
  httpRequestTime: state => state.httpRequestTime,
  httpResponseTime: state => state.httpResponseTime,
  navpos: state => state.navpos,
  auth: state => state.auth,
  authStatus: state => state.authStatus,
  loadStatus: state => state.loadStatus,
  isSouth: state => state.isSouth,
  episode: state => state.episode,
  areaCode: state => state.areaCode,
  liveStatus: state => state.liveStatus,
  noScroll: state => state.noScroll,
  indexKey: state => state.indexKey,
  appsNum:state => state.appsNum,
  downloadAppName: state=> state.downloadAppName
}

// 状态拦截
/**
 * 
 * mutation是同步的, 只要comit muation
 *
 * action 是异步的， 还有的错误处理也都在这里操作。
 */
const actions = {
  getRootPath({ commit }, status) {
    commit(types.GET_THEME, status)
  },
  getCatList({ commit }, status) {
    commit(types.GET_PROGRAMA, status)
  },
  getCategory({ commit }, status) {
    commit(types.GET_CATEGORY, status)
  },
  getIsHTTP({ commit }, status) {
    commit(types.GET_ISHTTPS, status)
  },
  getHttpRequestTime({ commit }, status) {
    commit(types.GET_HTTPREQUESTTIME, status)
  },
  getHttpResponseTime({ commit }, status) {
    commit(types.GET_HTTPRESPONSETIME, status)
  },
  getNavPos({ commit }, status) {
    commit(types.GET_NAVPOS, status)
  },
  getAuth({ commit }, status) {
    commit(types.GET_AUTH, status)
  },
  getLoadStatus({ commit }, status) {
    commit(types.GET_LOADSTATUS, status)
  },
  getIsSouth({ commit }, status) {
    commit(types.GET_ISSOUTH, status)
  },
  getEpisode({ commit }, status) {
    commit(types.GET_EPISODE, status)
  },
  getAuthStatus({ commit }, status) {
    commit(types.GET_AUTHSTATUS, status)
  },
  getAreaCode({ commit }, status) {
    commit(types.GET_AREACODE, status)
  },
  getLiveStatus({ commit }, status) {
    commit(types.GET_LIVESTATUS, status)
  },
  getAppsNum({ commit }, status) {
    commit(types.LOCALAPPSNUM, status)
  },
  getDownloadAppName({ commit }, status) {
    commit(types.DOWNLOADAPPNAME, status)
  }
}


/****
 * 
 * The only way to actually change state in a Vuex store is by committing a mutation,
 * 
 * 状态修改提交
 */
const mutations = {
  [types.GET_THEME](state, status) {
    state.rootPath = status
  },
  [types.GET_PROGRAMA](state, status) {
    state.catList = status
  },
  [types.GET_CATEGORY](state, status) {
    state.category = status
  },
  [types.GET_ISHTTPS](state, status) {
    state.isHTTPS = status
  },
  [types.GET_HTTPREQUESTTIME](state, status) {
    state.httpRequestTime = status
  },
  [types.GET_HTTPRESPONSETIME](state, status) {
    state.httpResponseTime = status
  },
  [types.GET_NAVPOS](state, status) {
    state.navpos = status
  },
  [types.GET_AUTH](state, status) {
    state.auth = status
  },
  [types.GET_LOADSTATUS](state, status) {
    state.loadStatus = status
  },
  [types.GET_ISSOUTH](state, status) {
    state.isSouth = status
  },
  [types.GET_EPISODE](state, status) {
    state.episode = status
  },
  [types.GET_AUTHSTATUS](state, status) {
    state.authStatus = status
  },
  [types.GET_AREACODE](state, status) {
    state.areaCode = status
  },
  [types.GET_LIVESTATUS](state, status) {
    state.liveStatus = status
  },
  [types.NO_SCROLL](state, status) {
    state.noScroll = status
  },
  [types.INDEXKEY](state, status) {
    state.indexKey = status
  },
  [types.LOCALAPPSNUM](state, status) {
    state.appsNum = status
  },
  [types.DOWNLOADAPPNAME](state, status) {
    state.downloadAppName = status
  }
}


export default {
  state,
  actions,
  getters,
  mutations
}



