// 监听键盘及遥控器按键
<template>
  <div class="addListenKeyDown">
  </div>
</template>
<script>
  import {client} from '../../js/client'
  const Kv = {};
  Kv.inputNo = 0;
  Kv.itemNo = 0;
  Kv.position = 0;
  Kv.keyboardSelected = false;

  if (typeof(VK_LEFT) == 'undefined') {
    var VK_LEFT = 0x25;
    var VK_UP = 0x26;
    var VK_RIGHT = 0x27;
    var VK_DOWN = 0x28;
  }
  if (typeof(VK_ENTER) == 'undefined') {
    var VK_ENTER = 0x0d;
  }
  if (typeof(VK_RED) == 'undefined') {
    var VK_RED = 0x74;
    var VK_GREEN = 0x75;
    var VK_YELLOW = 0x76;
    var VK_BLUE = 0x77;
  }
  if (typeof(VK_PLAY) == 'undefined') {
    var VK_PLAY = 0x50;
    var VK_PAUSE = 0x51;
    var VK_STOP = 0x53;
  }
  if (typeof(VK_FAST_FWD) == 'undefined') {
    var VK_FAST_FWD = 0x46;
    var VK_REWIND = 0x52;
  }
  if (typeof(VK_BACK) == 'undefined') {
    var VK_BACK = 0x08;
  }
  if (typeof(VK_0) == 'undefined') {
    var VK_0 = 0x30;
    var VK_1 = 0x31;
    var VK_2 = 0x32;
    var VK_3 = 0x33;
    var VK_4 = 0x34;
    var VK_5 = 0x35;
    var VK_6 = 0x36;
    var VK_7 = 0x37;
    var VK_8 = 0x38;
    var VK_9 = 0x39;
  }

  if (typeof(VK_CH_UP) == 'undefined') {
    var VK_CH_UP = 0x3D;
    var VK_CH_DOWN = 0x4D;
  }

  if (typeof(VK_HTML5_UP) == 'undefined') {
    var VK_HTML5_UP = 0x01;
    var VK_HTML5_DOWN = 0x02;
    var VK_HTML5_LEFT = 0x03;
    var VK_HTML5_RIGHT = 0x04;
    var VK_HTML5_BACK = 0x016;
  }
  // 检测数组中是否有某值
  Array.prototype.in_array = function(c){
      return  this.indexOf(c);
  }
  export default {
    data() {
      return {
        name: 'keyboard',
        isKeydown:'false',
      };
    },mounted() {
      
    },created: function () {
      window.goHome = this.goHome;
      this.initListen();
      // setTimeout(() => {        
      //   window.goHome = this.goHome;
      //   this.initListen();
      // }, 100);
    },activated(){ 
      this.initListen();
    },beforeDestroy(){
      // console.log("监听组件已移除");
      // window.document.onkeydown = null;
    
    },methods: {
      initListen() {
        let _this = this;
        window.document.onkeydown = function (e) {
          e = event || window.event || arguments.callee.caller.arguments[0];
          let keycode = e.which || e.keyCode;
            if (_this.handleKeyCode(keycode)) {
                e.preventDefault();
            }

        };
        // 　$(document).keydown(this.handleKeyCode(event.keyCode));
      },
      handleKeyCode(kc) {
        if($('.scroll_box_wrap').is(":animated")){return}
          console.log("kv:---" + kc);
        if (kc == VK_RIGHT || kc == VK_HTML5_RIGHT) {
            this.KeyRight();
            return true;
        } else if (kc == VK_LEFT || kc == VK_HTML5_LEFT) {
            this.KeyLeft();
            return true;
        } else if (kc == VK_ENTER) {
            this.KeyEnter();
            return true;
        } else if (kc == VK_UP || kc == VK_HTML5_UP) {
            this.KeyUp();
            return true;
        } else if (kc == VK_DOWN || kc == VK_HTML5_DOWN) {
            this.KeyDown();
            return true;
        } else if (kc == VK_BACK || kc == VK_HTML5_BACK || kc == 340 || kc == 27 || kc == 461 || kc == 8) {
            this.KeyBack();
            return true;
        } else if (kc == 513 || kc == 832 || kc == 835) {
            this.gotoIndex();
            return true;
        } else if (kc == 125 || kc == 415) {
            return true;
        } else if (kc == 127 || kc == 19) {
            return true;
        } else if(kc == 3 || kc==36){
           this.gotoIndex();
           return true;
        }
      },goHome(){
        console.log("监听到首页键值");
        if(!this.$store.getters.authStatus){
          console.log("开机认证不通过,不允许返回首页");
        }else{
          console.log("go to index page!");
          if(yh.isLive()){
             yh.finishOpen()
             return;
          }
          if(this.$route.name === 'index'){
            console.log('你已经在首页了');
          }else{
            this.$router.push({name:'index'});
          }
        }
        return false;
      },gotoIndex(){
        if(!this.$store.getters.authStatus){
           console.log("开机认证不通过,不允许返回首页");
        }else{
          console.log(this.$route);
          this.$router.push({name:'index'});
        }
      },
      KeyDown() {
        this.$emit('listenKeyCode', "down");
      },KeyUp(){
        this.$emit('listenKeyCode', "up");
      },KeyLeft(){
        this.$emit('listenKeyCode', "left");
      },KeyRight(){
        this.$emit('listenKeyCode', "right");
      },KeyEnter(){
        this.$emit('listenKeyCode', "KeyEnter");
      },KeyBack(){
          //  console.log("监听到返回键,返回上一级页面");
          this.$emit('listenKeyCode', "KeyBack");
        }
    }
  }
</script>



