<template>
  <div id="app">
    <!-- 这里是需要keepalive的 -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <!-- 这里不会被keepalive -->
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
  import {xha} from './js/xha.js'
  import {client as yh} from './js/client.js'
  import {Authentication as auth} from './js/auth.js'
  import {HTTPUtil as api} from './fetch/api.js'
  import {South}  from './fetch/south.js'
  export default {
   name: 'app',
   data(){
     return{
       isOnce:true,
     }
   },
   beforeCreate(){
     yh.initLivePlayer(0,0,544,310);  // 播放器初始化
     const devices = yh.deviceInfo(); // 设备信息初始化
     yh.setDeviceInfo(devices);
     this.$store.commit('GET_HTTPREQUESTTIME',new Date().getTime());
     const stbType = yh.getStbType();
     console.log("终端类型:"+stbType);
     yh.setStbType(stbType);
     yh.setApkStbType(stbType); // 将单播/组播字段传给APK
     console.log("BS保存终端类型:"+yh.stbType);
   },created(){
        console.log("请求认证操作");
        window.DeviceLogin = this.deviceLogin;
        window.getCategoryType= this.getCategoryType;
        window.timeAuthLogin = this.timeAuthLogin;
        let isSouth = localStorage.getItem("isSouth");
        if(isSouth!=undefined&&isSouth!=null){
           this.$store.commit('GET_ISSOUTH',true);
        }
        yh.HttpPost(auth.Login,auth.LoginParams(),'DeviceLogin'); // 终端AAA请求
        let url = '';
        if(this.isSouth){ // 区分豫南与豫北
          url = south.getCategoryHome();
        }else{
          url = api.getCategoryHome();
        }
        yh.HttpGet(url,'getCategoryType');// 请求筛选分类
        if (process.env.NODE_ENV === "development") { // 开发环境使用
          let params = {};
          params.url = "/api/Login";
          params.type = "post";
          params.data = JSON.parse(auth.LoginParams());
          api.ajax(params).then((jsonData)=>{
            this.deviceLogin(jsonData);
          })
          // api.get(url+'&returnType=jsonp').then((data)=>{
          //   this.getCategoryType(data);
          // })
        }else if(process.env.NODE_ENV === "pre"){
            this.$store.commit('GET_AUTH',true);
            this.$store.commit('GET_AUTHSTATUS',true);       
        }
       
   },mounted(){
        const clientH = document.documentElement.clientHeight || document.body.clientHeight;// 可见高度
        const clientW = document.documentElement.clientWidth || document.body.clientWidth;// 可见宽度
        const browser =  window.navigator.userAgent;
        console.log("设备可见高度："+clientH+"^^^^设备可见宽度"+clientW);
        console.log("终端信息:"+window.navigator.appVersion);
        console.log("userAgent:"+window.navigator.userAgent);
        if(browser.indexOf('Android') > -1 || browser.indexOf('Linux') > -1 || localStorage.getItem('bs_version') != null){ // 终端盒子访问
          
        }else{ // 其他非盒子浏览器访问
           this.$router.push({name:"dailog",params:{msg:4001}});
        }
   },activated() {
      console.log("Base 组件缓存"); 
   },
   methods:{
        deviceLogin(jsonData){
        console.log("AAA认证 返回");
        console.log(JSON.stringify(jsonData));
        if(jsonData.code==200){
          if(jsonData.result.resultCode=="0000"){
            try {
              yh.setAuthInfo(jsonData.result);
              console.log("区域分组:"+yh.areaCode);
              yh.setAreaGroup('0'+yh.areaCode);
              localStorage.setItem("AAA","true");
              localStorage.setItem("AuthResult",JSON.stringify(jsonData.result));
              this.$store.commit('GET_AUTH',true);
              this.$store.commit('GET_AUTHSTATUS',true);
              if(window.location.href.indexOf('/index')>0 || window.location.href.indexOf('/south')>0){ // 当url为首页的时候执行初始化操作
                console.log("开机鉴权成功!开启客户端定时任务");
                xha.init(); // xha埋点
                let cdnType  = yh.cdn;
                console.log("CDN类型"+cdnType);
                if(cdnType==='1'||cdnType===1){ // 豫北
                    api.get(api.getSysConfig()).then((jsonData)=>{
                      console.log("豫北配置："+JSON.stringify(jsonData));
                      if(jsonData.code===200){
                        let congfigOb = jsonData.data;
                        yh.timeTaskAuthAndOffLine(congfigOb.authTime,congfigOb.liveUpdateTime);// 定时认证
                        yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',Math.ceil(congfigOb.logHeartbeatTime/60));//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                      }else{
                         yh.timeTaskAuthAndOffLine(10800,300);// 定时认证
                         yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',30);//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                      }
                    }).catch(()=>{
                         console.log("请求豫北站点配置失败，启用默认配置");
                         yh.timeTaskAuthAndOffLine(10800,300);// 定时认证
                         yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',30);//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                    })
                }else if(cdnType==='2'||cdnType===2){ // 豫南 
                    console.log("CDN==2 豫南站点，华为CDN")
                    this.$store.commit('GET_ISSOUTH',true);
                    localStorage.setItem("isSouth",true);
                     api.get(South.getSysConfig()).then((jsonData)=>{
                      console.log("豫南配置："+JSON.stringify(jsonData));
                      if(jsonData.code===200){
                        let congfigOb = jsonData.data;
                        yh.timeTaskAuthAndOffLine(congfigOb.authTime,congfigOb.liveUpdateTime);// 定时认证
                        yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',Math.ceil(congfigOb.logHeartbeatTime/60));//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                      }else{
                         yh.timeTaskAuthAndOffLine(10800,300);// 定时认证
                         yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',30);//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                      }
                    }).catch(()=>{
                         console.log("请求豫南站点配置失败，启用默认配置");
                         yh.timeTaskAuthAndOffLine(10800,300);// 定时认证
                         yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',30);//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                    })
                }else{
                  console.log("获取CDN类型失败，启动默认");
                  yh.timeTaskAuthAndOffLine(10800,300);// 定时认证
                  yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',30);//2018/8/3，15:55 王浩要求多加一个时间参数星红桉采集时间
                }
                console.log("开机认证执行结束!");
              }else{
                console.log("非首页刷新加载!");
                  $('.loading').show(); // 非首页加载刷新展示loading
              }
            } catch (error) {
               console.log("鉴权成功，处理参数转换异常");
            }
          }else{
            localStorage.setItem("AAA","false");
            this.$store.commit('GET_AUTH',false);
            this.$store.commit('GET_AUTHSTATUS',false);
            yh.close_s_Video(); // 如果用户在小窗口观看中，认证失败,需要关闭直播小窗口
            yh.stop(); // 如果用户在小窗口观看中，认证失败,需要关闭点播小窗口
            this.$router.push({name:"dailog",params:{msg:jsonData.result.resultMessage}});
          }
        }else{// 返回异常情况，放行进入首页，待第二次鉴权
          if(jsonData.code===0||jsonData.code==='0'){
              if(jsonData.message){
                  if(jsonData.message.indexOf('SSLHandshakeException')>=0){ // https 证书时间同步失败
                     console.log('BS 捕获客户端https 时间校验不同步 请求失败，开始请求HTTP')
                     console.log('HTTP认证地址:'+auth.httpLogin);
                     yh.HttpPost(auth.httpLogin,auth.LoginParams(),'DeviceLogin'); // 终端AAA请求
                      // localStorage.setItem("AAA","false");
                      // this.$store.commit('GET_AUTH',false);
                      // this.$store.commit('GET_AUTHSTATUS',false);
                      // yh.close_s_Video(); // 如果用户在小窗口观看中，认证失败,需要关闭直播小窗口
                      // yh.stop(); // 如果用户在小窗口观看中，认证失败,需要关闭点播小窗口
                      // this.$router.push({name:"dailog",params:{msg:"本地时间校验失败,请重启机顶盒！",code:101}});
                      return;
                  }
              }
          }
           console.log("首次认证异常，判断本地缓存中是否存在认证成功信息");
           const aaaResult = localStorage.getItem("AuthResult");
           if(aaaResult){
             console.log("首次认证异常，且本地有缓存，取缓存");
             let aaa = JSON.parse(aaaResult);
             if(aaa.resultCode==="0000"){
                console.log("使用本地缓存成功");
                localStorage.setItem("AAA","true");
                this.$store.commit('GET_AUTH',true);
                yh.setAuthInfo(aaa);
                xha.init(); // xha埋点
             }
           }else{
             localStorage.setItem("AAA","false");
             this.$store.commit('GET_AUTH',false);
             xha.init(); // xha埋点
           }
           console.log("启用默认站点配置信息");
           yh.timeTaskAuthAndOffLine(10800,300);// 定时认证
           yh.authTask(auth.Login,auth.LoginParams(),'timeAuthLogin',30);
        }
      },getCategoryType(data){ // 获取一级栏目分类
          console.log("首页获取一级分类数据返回");
          // console.log(JSON.stringify(data));
          try {
             let categoryArr = [];
              data.data.categoryList.map((item) =>{
                let obj = {};
                obj.categoryName = item.categoryName;
                obj.categoryType = item.catCode
                categoryArr.push(obj)
              });
            window.localStorage.setItem('categoryArr',JSON.stringify(categoryArr));
            window.localStorage.setItem('filterCategoryArr',JSON.stringify(data));
          } catch (error) {
            console.log("查询一级栏目分类异常");
          }
         
      },timeAuthLogin(jsonData){ // 定时认证回调函数
        let jsonOb = null;
        if(typeof(jsonData)==="object"){
          jsonOb = jsonData;
        }else{
          jsonOb =  JSON.parse(jsonData);
        }
        console.log("AAA定时认证返回");
        console.log(JSON.stringify(jsonOb));
        if(jsonOb.code==200){
          if(jsonOb.result.resultCode=="0000"){
            try {
              yh.setAuthInfo(jsonOb.result);
              localStorage.setItem("AAA","true");
              localStorage.setItem("AuthResult",JSON.stringify(jsonOb.result));
              this.$store.commit('GET_AUTH',true);
              this.$store.commit('GET_AUTHSTATUS',true);
            } catch (error) {
               console.log("鉴权成功，处理参数转换异常");
            }
          }else{
            localStorage.setItem("AAA","false");
            this.$store.commit('GET_AUTH',false);
            this.$store.commit('GET_AUTHSTATUS',false);
            yh.close_s_Video(); // 如果用户在小窗口观看中，认证失败,需要关闭直播小窗口
            yh.stop(); // 如果用户在小窗口观看中，认证失败,需要关闭点播小窗口
            this.$router.push({name:"dailog",params:{msg:jsonOb.result.resultMessage}});
          }
        }else{// 返回异常情况，放行进入首页，待第二次鉴权
           console.log("定时认证返回异常,暂不处理等待下一次认证。");
          //  localStorage.setItem("AAA","false");
          //  this.$store.commit('GET_AUTH',false);
        }
      }
    },watch: {
      '$route' (to, from) {
        if(to.name==="south"){
           this.$store.commit('GET_ISSOUTH',true);
           localStorage.setItem("isSouth",true);
        }
      }
    }
  }
</script>
<style>
  @import url(./font/iconfont1.css);
</style>



