var HTTPUtil = {};

HTTPUtil.get =  function (url) {
  console.log("版本标识:"+process.env.NODE_ENV);
  if (process.env.NODE_ENV === "development" || process.env.NODE_ENV === "pre"){ // 开发环境及杭州测试环境
   
    url = url.replace("https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1","http://47.96.85.147/epgds/?s=1000|1");
    url = url.replace("https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2", "http://47.96.85.147/epgds/?s=1001|2");
    url = url.replace("http://183.131.15.11:8382", "http://47.96.85.147/epgds");
    // if(url.indexOf('&returnType=jsonp') == -1){
    //   url+='&returnType=jsonp';
    // }
    console.log("杭州测试环境："+url);
    
  } else if (process.env.NODE_ENV === "local"){ // 河南测试环境
    if (url.indexOf('https://dx-cmsyb-dbds.hilooktv.com') >= 0) {
      // 豫北

      url = url.replace('https://dx-cmsyb-dbds.hilooktv.com:43301', 'http://123.163.119.53:18001')
    } else if (url.indexOf('https://dx-cmsyn-dbds.hilooktv.com') >= 0) {
      // 豫南

      url = url.replace('https://dx-cmsyn-dbds.hilooktv.com:43301', 'http://123.163.119.53:18001')
    }
    console.log("河南测试环境:"+url);
  }else{
    // console.log("生产环境："+url);
  }
  // 是否是预发布版本
  if(yh.isPreRelease()){ 
    if (url.indexOf('?')>0){
      url += "&ready=1";
    }else{
      url += "?reday=1";
    }
    console.log("预发布版本请求地址" + url);
  }
  // 请求地址增加mac信息
  
  // if (url.indexOf('?') > 0) {
  //   url += '&mac=' + Base64.encode(yh.mac);
  // } else {
  //   url += "?mac=" + Base64.encode(yh.mac);
  // }
  console.log("url:" + url);
  // jsonp 请求方式
  return new Promise(function (resolve, reject) {
      fetchJsonp(url, {
      jsonpCallback: 'jsonpCallback',
      // jsonpCallbackFunction:'jsonpCallback',
      timeout:15000
      }).then(function (response) {
      //  console.log("接口数据已收到，返回给页面" + JSON.stringify(response));
        if (response.ok) {
          return response.json();
        } else {
          reject({status:response.status });
        }
      })
      .then(function (response) {
        // console.log("json数据："+JSON.stringify(response));
        resolve(response);
      })
      .catch(function (ex) {
        console.log(ex);
        reject({ status: -1 });
      })
  })


  // get cros 请求方式
  // return new Promise((resovle, reject) => {
  //   $.ajax({
  //     type: "get",
  //     dataType: "json",
  //     async:true,
  //     url: url.replace("&returnType=jsonp",""),
  //     success: res => {
  //       resovle(res);
  //     },
  //     error: err => {
  //       reject(err);
  //     }
  //   });
  // });
}



/*
  定义一个使用promise的ajax请求,这里依赖jquery
  参数中请求url为必填参数
  开发使用
*/
HTTPUtil.ajax = param => {

  return new Promise((resovle, reject) => {
    $.ajax({
      "type": param.type || "get",
      "dataType": 'json',
      "contentType": "application/json",
      "async": param.async || true,
      "url": param.url,
      "data": JSON.stringify(param.data) || "",
      "success": res => {
        resovle(res);
      },
      "error": err => {
        reject(err);
      }
    })
  })
}

