// 获取栏目筛选结果

HTTPUtil.getFilterResult = function (condition, index, pageSize,vipCode,vip,specialCode,topCatCode) {
  let url = '';
    if (vipCode == undefined && vip == undefined) {
      // 电影，电视剧筛选接口
      url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize;
    } else {
      if (specialCode != undefined && specialCode != '' && vip != undefined) {
        // 优品
        url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vip=" + vip + "&vipCode=" + specialCode;
      } else if (vipCode !== undefined && vip !== undefined) {
        // 专区
        if(topCatCode){
          url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vip=" + vip +'&topCatCode=' +topCatCode + '&siteCode=YB';
        }else{
          if(vipCode == 'YPYS' && specialCode == ''){
            vip = 1;
          }else{
            vip = 0;
          }
          url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vipCode=" + vipCode + "&vip=" + vip;
        }
      }
      if (vipCode == undefined && vip !== undefined) {
        url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vip=" + vip;
      };
       if (vipCode !== undefined && vip == undefined) {
        if(topCatCode){
          url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + '&topCatCode=' +topCatCode + '&siteCode=YB';
        }else{
          //点播 ，电视剧
          url = "https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vipCode=" + vipCode+ "&vip=0" ;
        }
      };
    }
  
  return HTTPUtil.get(url);
};

