/****
 *  支付回调地址
 */
HTTPUtil.paymentUrl = function(){
  let url = "";
  if (process.env.NODE_ENV ==="development"){
     url = "http://localhost:8088/henan/payment?payBack=false";
  }else{
    const cUrl = location.href;
    if (cUrl.indexOf('dx-bsyb.hilooktv.com') >= 0) { // 豫北
      url = "https://dx-bsyb.hilooktv.com:43301/henan/payment?payBack=false";
    } else if (cUrl.indexOf('123.163.119.53') >= 0){
      url = "http://123.163.119.53:8281/henan/payment?payBack=false"
    }else{
      url = "https://dx-bsyn.hilooktv.com:43301/henan/payment?payBack=false";
    }
  }
  return url;
}


