// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router/index.js'
import store from './store/store'
import VueLazyImage from '../src/tool/lazyLoad/index.js'
import keyDo from '../src/components/keyCode/index.js'
import lodash from 'lodash'
import { hybrid } from './js/hybrid.js'

Vue.use(keyDo)
Vue.use(VueLazyImage,{debounce:200})
Vue.use(lodash)

Vue.prototype.$hybrid = hybrid

window.oncontextmenu = function (event) { // 禁止右键查看源代码
  event.preventDefault();
  return false;
}

// const isDebug_mode = process.env.NODE_ENV !== 'production';

let isDebug_mode = false;
if (process.env.NODE_ENV === "development" || process.env.NODE_ENV === "pre"){
  isDebug_mode = true;
}
Vue.config.debug = isDebug_mode;
Vue.config.devtools = isDebug_mode;
Vue.config.productionTip = isDebug_mode;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App}
})



