// 全屏打开直播窗口
/****
 * @param allChannelUrl 所有频道的Url
 * @param channeelType 直播频道的类型 央视、 卫视、 其他等 *
 * @param ChannelId 直播频道的Id *
 * @param type 点击哪种类型频道 0 热门频道、 1 预约、 2 常看频道、 3 直播全频道、 4 回看等 *
 * @param burd 是否是从推荐位回看进入 *
 * @param changeurl 如果isBackToSee为true, 表示为推荐也回看的数据url *
 * @param toBig 是否切换大屏 *
 * @param isSmallWindow 是否直播小窗口进入 *
 * @return 是否成功， 0 - 失败， 1 - 成功
 *  
 */
yh.prototype.fullLivePlay = function (allChannelUrl, type, channelType, channelId, burd, changeurl, toScreen,toBig) {
   console.log("直播播放器POPUP_PLAYER_ACTION.playerLive：allChannelUrl:" + allChannelUrl + "^^^^^^^" + "channelType:" + type + "^^^^liveChannelType:" + channelType + "^^^^^channelId:" + channelId + "^^^^IsBackSee" + burd + "^^^^^^backSeeUrl:" + changeurl + "^^^^历史记录回调：smallCallBack" + "^^^toScreen:" + toScreen + "^^^^^toBig:" + toBig);
    try {
        xha.heart(0); // 关闭js心跳接口
        return POPUP_PLAYER_ACTION.playerLive(allChannelUrl, type, channelType, channelId, burd, changeurl, 'smallCallBack', toScreen,toBig);
    } catch (error) {
        console.log("调用全屏直播异常");
    }
}

