// 播放器选集
yh.prototype.play = function (params) {
    // console.log("播放媒体信息:"+params);
    try {
      let obj = JSON.parse(params);
      let numStartTime =parseInt(obj.startTime);
      let acsSort = obj.jsonUrl;
      if (acsSort.indexOf('itemSort') >= 0) {

      }else{
           acsSort = obj.jsonUrl + "&itemSort=1"; // itemSort=1为正序，2为倒序
      }
      xha.heart(0); // 关闭js心跳接口
      let success = POPUP_VOD_BRIDGE.vodPlay(obj.buder, acsSort, obj.index, numStartTime, obj.preView, obj.callback, "changeEpisode");
      console.log("POPUP_VOD_BRIDGE.vodPlay()返回："+success);
    } catch (error) {
        console.log("POPUP_VOD_BRIDGE.vodPlay()异常");
    }
}

