/**
 * 终端打开新的webView 传入callback
 * @param {*} url 
 * @param {*} callback 
 */
yh.prototype.openPage = function (url,callback) {
  try {
    HyBirdWebCallBack.showPage(url, callback);
  } catch (error) {
    console.log("打开新的activity webView页面异常");
  }
}

