/**
 *date: 2019.01.22
 *  王浩要求在特殊情况下页面要判断通知客户端直播不需要发送直播星红桉日志
 *  1.在组播环境下
 *  2.在直播小屏切换大屏的时候
 *  3.在直播频道点击:全部频道,热门频道，频道回看
 *  4.在大屏切换小屏重新打开播放器的时候不需要回调
 */
yh.prototype.isSendCsXhaLog =  function (params) {
    try {
        console.log('POPUP_PLAYER_ACTION.isPlayerSendXha符合不发送星红桉条件情况通知客户端:' + params)
        POPUP_PLAYER_ACTION.isPlayerSendXha(params);
    } catch (error) {
        console.log("通知客户端小屏切换不要发送星红桉直播日志:"+params);
    }
}
/**
 * date 2019.12.22
 *   需要通知客户端不在小窗口播放的时候需要发退出日志；
 *   小窗口播放的时候不需要发退出日志
 * 
 */
yh.prototype.isLiveStopXhaLog =  function(isPlaying){
    try {
        console.log('POPUP_PLAYER_ACTION.isStopXha小窗口退出不要发日志：' + isPlaying)
        POPUP_PLAYER_ACTION.isStopXha(isPlaying)
    } catch (error) {
        console.log('通知客户端是否需要发送直播退出日志:'+isPlaying);
    }
}


