import fetchJsonp from 'fetch-jsonp';
import {client as yh} from '../js/client.js'

/**
 * 2017-11-30
 * 基于fetch封装get jsonp请求请求
 *
 */

var South = {};


South.get = function (url) {
  if (process.env.NODE_ENV === "development" || process.env.NODE_ENV === "pre") { // 开发环境及杭州测试环境

    url = url.replace("https://dx-cmsyb-dbds.hilooktv.com:43301/?s=1000|1", "http://183.131.15.11:8382/?s=1000|1");

    url = url.replace("https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2", "http://183.131.15.11:8382/?s=1001|2");

  } else if (process.env.NODE_ENV === "local") { // 河南测试环境

    if (url.indexOf('https://dx-cmsyb-dbds.hilooktv.com') >= 0) { // 豫北

      url = url.replace("https://dx-cmsyb-dbds.hilooktv.com:43301", "http://123.163.119.53:18001");

    } else if (url.indexOf('https://dx-cmsyn-dbds.hilooktv.com') >= 0) { // 豫南

      url = url.replace("https://dx-cmsyn-dbds.hilooktv.com:43301", "http://123.163.119.53:18001");
    }
  }

  if (yh.isPreRelease()) { // 是否是预发布版本
    if (url.indexOf('?') > 0) {
      url += "&ready=1";
    } else {
      url += "?reday=1";
    }
    console.log("预发布版本请求地址" + url);
  }
  console.log("url:" + url);
  return new Promise(function (resolve, reject) {
    fetchJsonp(url, {
        jsonpCallback: 'jsonpCallback',
        // jsonpCallbackFunction: 'jsonpCallback',
        timeout: 15000
      })
      .then(function (response) {
        if (response.ok) {
          return response.json();
        } else {
          reject({
            status: response.status
          });
        }
      })
      .then(function (response) {
        resolve(response);
      })
      .catch(function (ex) {
        console.log(ex);
        reject({
          status: -1
        });
      })
  })

  // return new Promise((resovle, reject) => {
  //   $.ajax({
  //     type: "get",
  //     dataType: "json",
  //     async: true,
  //     url: url.replace("&returnType=jsonp", ""),
  //     success: res => {
  //       resovle(res);
  //     },
  //     error: err => {
  //       reject(err);
  //     }
  //   });
  // });
}


/*
  定义一个使用promise的ajax请求,这里依赖jquery
  参数中请求url为必填参数
*/
South.ajax = param => {

  console.log(param);
  return new Promise((resovle, reject) => {
    $.ajax({
      "type": param.type || "get",
      "dataType": 'json',
      "contentType": "application/json",
      "async": param.async || true,
      "url": param.url,
      "data": JSON.stringify(param.data) || "",
      "success": res => {
        resovle(res);
      },
      "error": err => {
        reject(err);
      }
    })
  })
}


// 获取主题接口
South.getTheme = function (url) {
  return South.get(url);
};

