// 获取栏目筛选条件
South.getFilterCategory = function (catCode,vipCode,vip,topCatCode,siteCode) {
  let url = '';
    if (vipCode == undefined || vip == undefined) {
      if(topCatCode){
        url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=yhCategoryList&k=1&v=1&returnType=jsonp&catCode=" + catCode + '&topCatCode='+topCatCode+'&siteCode='+siteCode;
      }else{
        url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=yhCategoryList&k=1&v=1&returnType=jsonp&catCode=" + catCode;
      }
    } else {
      url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=yhCategoryList&k=1&v=1&returnType=jsonp&catCode=" + catCode + '&vipCode=' + vipCode + '&vip=' + vip;
    }
  
  return South.get(url);
};

// 获取栏目筛选结果
South.getFilterResult = function (condition, index, pageSize,vipCode,vip,specialCode,topCatCode) {
  let url = '';
      // debugger

    if (vipCode == undefined && vip == undefined) {
      // 电影，电视剧筛选接口
      url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize;
    } else {
      if (specialCode != undefined && specialCode != '' && vip != undefined) {
        // 优品
        url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vip=" + vip + "&vipCode=" + specialCode;
      } else if (vipCode !== undefined && vip !== undefined) {
        // 专区
        if(topCatCode){
          url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vip=" + vip +'&topCatCode=' +topCatCode + '&siteCode=YN';
        }else{
          if(vipCode == 'YPYS' && specialCode == ''){
            vip = 1;
          }else{
            vip = 0;
          }
          url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vipCode=" + vipCode + "&vip=" + vip;
        }
      };
      if (vipCode == undefined && vip !== undefined) {
        url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vip=" + vip;
      };
      if (vipCode !== undefined && vip == undefined) {
        if(topCatCode){
          url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + '&topCatCode=' +topCatCode + '&siteCode=YN';
        }else{
          //点播 ，电视剧
          url = "https://dx-cmsyn-dbds.hilooktv.com:43301/?s=1001|2&p=hnhkScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&vipCode=" + vipCode;
        }
      };
    
    console.log(url)
  }
  return South.get(url);
}

